.Shape-selector {
    display: flex;
    margin-top: 24px;
}

.Shape-selector .Button-wrapper {
    position: relative;
    width: 160px;
    margin-left: 24px;
}

.Shape-selector button {
    position: relative;
    background: tomato;
    color: #222;
    font-size: 24px;
    border: none;
    outline: none;
    box-shadow: 7px 7px #222;
    padding: 5px 0px;
    width: 100%;
}

.Shape-selector button:hover {
    outline-offset: -2px;
    outline: solid 2px #222;
}

.Shape-selector button:focus {
    outline-offset: -2px;
    outline: solid 2px #222;
}

.Shape-selector .Button-icon {
    position: absolute;
    top: 5px;
    right: 5px;
    width: 6px;
    height: 18px;
    border: solid #fff;
    border-width: 6px 3px 0px 0px;
    pointer-events: none;
}

.Shape-selector button:active {
    transform: scale(0.95, 0.95);
    box-shadow: none;
}

.Shape-selector button:active+.Button-icon {
    transform: scale(0.95, .095);
    right: 10px;
}